@import 'legacy';

.video-wrapper {
  float: none;
  clear: both;
  width: 100%;
  position: relative;
  // 16:9
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important; } }


