img {
  width: 100%;
  height: auto; }


.article--video-img {
  //override default 500px width set by default by the class - inherited from _legacy.scss
  width: 100%; }


@import '1-tools/mixins';

button {
  &.button-secondary {
    height: 44px;
    padding: 0 40px;
    margin: 0 auto;
    background-color: #6ba2aa;
    border-radius: 10px;
    border: 1px solid #fce87c;
    color: #fce87c;
    text-transform: uppercase;
    @include transition(all .2s ease-in-out);
    &:hover {
      color: #6ba2aa;
      background: #fce87c;
      border: 2px solid #fce87c; } } }




