$color_1: #000;
$color_2: inherit;
$color_3: #fff;
$color_4: #999;
$color_5: #6BA2AA;
$color_6: #9ecbd2;
$color_7: white;
$color_8: #6ba2aa;
$font-family_1: sans-serif;
$font-family_2: monospace, monospace;
$font-family_3: "Open Sans", Helevetica, Arial, sans-serif;

