html {
	font-family: $font-family_3;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	input[type="button"] {
		-webkit-appearance: button;
		cursor: pointer;
	}
	input[disabled] {
		cursor: default;
	}
}
body {
	margin: 0;
	background-color: #fff;
	color: $color_1;
	font-family: $font-family_3;
	font-size: 16px;
	line-height: 1.5;
}
article {
	display: block;
}
aside {
	display: block;
}
details {
	display: block;
}
figcaption {
	display: block;
}
figure {
	display: block;
	margin: 1em 40px;
}
footer {
	display: block;
}
header {
	display: block;
}
hgroup {
	display: block;
}
main {
	display: block;
}
menu {
	display: block;
}
nav {
	display: block;
}
section {
	display: block;
}
summary {
	display: block;
}
audio {
	display: inline-block;
	vertical-align: baseline;
	&:not([controls]) {
		display: none;
		height: 0;
	}
}
canvas {
	display: inline-block;
	vertical-align: baseline;
}
progress {
	display: inline-block;
	vertical-align: baseline;
}
video {
	display: inline-block;
	vertical-align: baseline;
}
[hidden] {
	display: none;
}
template {
	display: none;
}
a {
	background-color: transparent;
	&:active {
		outline: 0;
	}
	&:hover {
		outline: 0;
	}
}
abbr[title] {
	border-bottom: 1px dotted;
}
b {
	font-weight: bold;
}
strong {
	font-weight: bold;
}
dfn {
	font-style: italic;
}
h1 {
	font-size: 2em;
	margin: 0.67em 0;
	font-weight: 700;
	margin-bottom: 5px;
	margin-top: 0;
	font-size: 38px;
}
mark {
	background: #ff0;
	color: $color_1;
}
small {
	font-size: 80%;
}
sub {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	bottom: -0.25em;
}
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	top: -0.5em;
}
img {
	border: 0;
}
svg {
	&:not(:root) {
		overflow: hidden;
	}
}
hr {
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	height: 0;
}
pre {
	overflow: auto;
	font-family: $font-family_2;
	font-size: 1em;
}
code {
	font-family: $font-family_2;
	font-size: 1em;
}
kbd {
	font-family: $font-family_2;
	font-size: 1em;
}
samp {
	font-family: $font-family_2;
	font-size: 1em;
}
button {
	color: $color_2;
	font: inherit;
	margin: 0;
	overflow: visible;
	text-transform: none;
	-webkit-appearance: button;
	cursor: pointer;
	&::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
}
input {
	color: $color_2;
	font: inherit;
	margin: 0;
	line-height: normal;
	&::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
}
optgroup {
	color: $color_2;
	font: inherit;
	margin: 0;
	font-weight: bold;
}
select {
	color: $color_2;
	font: inherit;
	margin: 0;
	text-transform: none;
}
textarea {
	color: $color_2;
	font: inherit;
	margin: 0;
	overflow: auto;
}
input[type="reset"] {
	-webkit-appearance: button;
	cursor: pointer;
}
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}
button[disabled] {
	cursor: default;
}
input[type="checkbox"] {
	box-sizing: border-box;
	padding: 0;
}
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}
input[type="number"] {
	&::-webkit-inner-spin-button {
		height: auto;
	}
	&::-webkit-outer-spin-button {
		height: auto;
	}
}
input[type="search"] {
	-webkit-appearance: textfield;
	-moz-box-sizing: content-box;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
	&::-webkit-search-cancel-button {
		-webkit-appearance: none;
	}
	&::-webkit-search-decoration {
		-webkit-appearance: none;
	}
}
fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}
legend {
	border: 0;
	padding: 0;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
td {
	padding: 0;
}
th {
	padding: 0;
}
h2 {
	font-weight: 700;
	margin-bottom: 5px;
	margin-top: 0;
	color: $color_3;
	font-size: 20px;
	margin-bottom: 20px;
	text-transform: uppercase;
}
h3 {
	font-weight: 700;
	margin-bottom: 5px;
	margin-top: 0;
	font-size: 18px;
	text-transform: uppercase;
}
h4 {
	font-weight: 700;
	margin-bottom: 5px;
	margin-top: 0;
}
h5 {
	font-weight: 700;
	margin-bottom: 5px;
	margin-top: 0;
}
h6 {
	font-weight: 700;
	margin-bottom: 5px;
	margin-top: 0;
}
blockquote {
	color: $color_4;
	font-size: 18px;
	font-style: italic;
}
p {
	margin-bottom: 20px;
	margin-top: 0;
}
.article {
	padding-bottom: 40px;
	padding-top: 40px;
}
.article-body {
	padding-left: 20px;
	padding-right: 20px;
}
.article-title {
	border-bottom: 3px solid #6ba2aa;
	margin-bottom: 20px;
	padding-bottom: 10px;
	text-align: center;
}
.article--picture-author {
	border-bottom: 2px solid #6BA2AA;
	color: $color_5;
	font-size: 16px;
	padding: 0 20px 20px;
	margin: 0 0 30px;
	text-transform: uppercase;
}
.article--picture-img {
	width: 500px;
	margin: auto;
	>img {
		max-width: 100%;
	}
}
.article--video-img {
	width: 500px;
	margin: auto;
	>img {
		max-width: 100%;
	}
}
.cell {
	margin: 0 auto;
	max-width: 800px;
}
.comment {
	margin-bottom: 40px;
	>img {
		border: 2px solid #45686D;
		border-radius: 50%;
		float: left;
		height: 64px;
		width: 64px;
		margin: 0 10px 0 0;
	}
}
.comment-actions {
	color: $color_6;
	font-size: 12px;
	text-transform: uppercase;
	>a {
		margin-left: 15px;
		&:hover {
			color: $color_3;
		}
	}
	>span {
		margin-left: 15px;
	}
	a {
		color: $color_6;
		text-decoration: underline;
		transition: 0.2s ease-in-out;
	}
}
.comment-confirm {
	display: inline-block;
	margin-left: 15px;
}
.comment-body {
	color: $color_3;
}
.comment-count {
	color: $color_6;
	margin-bottom: 20px;
}
.comment-button {
	background-color: #45686D;
	border: none;
	border-radius: 4px;
	color: $color_7;
	cursor: pointer;
	float: right;
	font-size: 13px;
	font-weight: bold;
	padding: 6px 12px;
	text-transform: uppercase;
	outline: none;
	width: 145px;
	&:active {
		background-color: #5C8288;
	}
}
.comment-list {
	margin-top: 40px;
}
.comment-header {
	border-bottom: 2px solid #fff;
	color: $color_3;
	margin-bottom: 10px;
	padding-bottom: 5px;
}
.comment-form {
	background-color: #9ecbd2;
	border-radius: 10px;
	margin-bottom: 40px;
	padding: 20px;
	position: relative;
	&::before {
		background: url("../assets/images/illo-gears.svg") center no-repeat;
		content: '';
		height: 80px;
		position: absolute;
		right: 0;
		top: -80px;
		width: 154px;
	}
	>label {
		color: $color_8;
		display: block;
		margin-bottom: 10px;
		text-transform: uppercase;
	}
}
.comment-form-actions {
	>button {
		background-color: #fce87c;
		border: 0;
		border-radius: 10px;
		color: $color_8;
		display: inline-block;
		font-weight: 700;
		line-height: 3;
		padding: 0 40px;
		text-transform: uppercase;
		transition: 0.2s ease-in-out;
		&:active {
			background-color: #fbe259;
		}
		&:focus {
			background-color: #fbe259;
		}
		&:hover {
			background-color: #fbe259;
		}
	}
}
.comment-form-fields {
	>input {
		border: 0;
		border-radius: 10px;
		box-sizing: border-box;
		margin-bottom: 10px;
		padding: 10px;
		width: 100%;
		outline: none;
		resize: none;
	}
	>textarea {
		border: 0;
		border-radius: 10px;
		box-sizing: border-box;
		margin-bottom: 10px;
		padding: 10px;
		width: 100%;
		outline: none;
		resize: none;
	}
}
.row {
	background-color: #6ba2aa;
	overflow: hidden;
	padding-bottom: 40px;
	padding-top: 40px;
}
.row.comments-container {
	min-height: 300px;
}
.comment-avatars {
	margin-bottom: 40px;
	>h4 {
		color: $color_6;
		text-transform: uppercase;
	}
	>ul {
		margin: 0;
		padding: 0;
		list-style: none;
		>li {
			margin: 0;
			padding: 0;
			list-style: none;
			display: inline-block;
			&~li {
				margin: 0 5px;
			}
		}
	}
	img {
		border: 2px solid #45686D;
		border-radius: 50%;
		height: 64px;
		width: 64px;
	}
}
.top-menu {
	height: 35px;
	>ul {
		background-color: #3E7780;
		margin: 0;
		padding: 5px 10px;
		list-style: none;
		text-align: center;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 1;
		>li {
			display: inline-block;
			margin: 0 5px;
			padding: 0 15px 0 10px;
			border-right: 1px solid white;
			font-size: 14px;
			color: $color_7;
			text-transform: uppercase;
			&:last-child {
				border: none;
			}
			a {
				color: $color_7;
				text-decoration: none;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}
@media screen and (max-width: 800px) {
	.row.comments-container {
		padding-left: 40px;
		padding-right: 40px;
	}
}
